import { LitElement, html } from '@polymer/lit-element'
import { Chart } from 'chart.js'

export default class ChartJS extends LitElement {
  static get is() {
    return 'things-chartjs'
  }

  static get properties() {
    return {
      options: Object
    }
  }

  constructor() {
    super()
  }

  firstUpdated() {
    var chartEl = this.shadowRoot.querySelector('#chart')

    console.log(`firstUpdated ${chartEl}`)
    var ctx = chartEl.getContext('2d')

    var chart = new Chart(ctx, this.options)
  }

  render() {
    return html`
      <canvas id="chart" width="${this.width}" height="${this.height}"></canvas>
    `
  }
}

customElements.define(ChartJS.is, ChartJS)
